.column{
	display: flex;
	flex-direction: column;
	position: relative;
}
.label{
	font-family: "Oswald", sans-serif;
	font-size: 12px;
	color: #ABB9C7;
	position: absolute;
	left: 16px;
	top: 7px;
}
.input{
	width:cal(100% - 16px);
	padding-left: 16px;
	padding-right: 16px;
	border-radius: 4px;
	border: 1px solid #eeee;
	font-size: 15px;
	height: 56px;
	font-size: 17px;
	border: 1px solid #ABB9C780;
	color: #413E4D;
}
.button_general{
	background-color: #DD3542;
	color: white;
	border-radius: 24px;
	width: 174px;
    height: 48px;
    border: none;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
}
.button_general_icon{
	font-size: 24px;
	position: absolute;
	left: 15px;
}
.button_send{
	background-color: #DD3542;
	color: white;
	border-radius: 24px;
	width: 174px;
    height: 48px;
    border: none;
}